import {useState, useEffect} from 'react';
import Item from './Item';
import {TextField, Button, Paper, List} from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';


const Items = () => {

    const [items, setItems] = useState();
    const [name, setName] = useState();
    const [description, setDescription] = useState();
    const [link, setLink] = useState();
    const [price, setPrice] = useState();
    const [auth, setAuth] = useState();

    const useStyles = makeStyles((theme) => ({
        root: {
          '& .MuiTextField-root': {
            margin: theme.spacing(1),
            width: '15ch'
          },
          '& .MuiButton-root': {
            margin: theme.spacing(1),
            width: '15ch'
          },
        },
      }));

    const classes = useStyles();

    const getData = () => {
        fetch('https://wishlist-40fdd-default-rtdb.firebaseio.com/wishlist.json')
            .then(res => res.json())
            .then(res => setItems(res))
    }

    const data = {
        "name": name,
        "description": description,
        "link": link,
        "price": price
      };

    const addData = (e) => {
        e.preventDefault();
        if(name.trim() === '' || auth !== 'barettjones'){
            return;
        }
        setName('');
        setDescription('');
        setLink('');
        setPrice('');

        const requestOptions = {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify(data)
        };
        fetch('https://wishlist-40fdd-default-rtdb.firebaseio.com/wishlist.json', requestOptions)
            .then(response => response.json())
            .then(setTimeout(function() {
                getData();
            }, 500))
    }

    useEffect(() => {
        getData();
    },[]);
    

    return(
        <div>
            <List>
                {(items && Object.keys(items).length !== 0) ? Object.keys(items).map((key, index) => {return <Item key={key} itemkey={key} name={items[key].name} description={items[key].description} link={items[key].link} price={items[key].price}/>}) : "No Data"}
            </List>

            <form className={classes.root} noValidate autoComplete="off">
                <Paper elevation={3}>
                    <TextField label="Name" value={name} onChange={(e) => setName(e.target.value)}/>
                    <TextField label="Description" value={description} onChange={(e) => setDescription(e.target.value)}/>
                    <TextField label="Link" value={link} onChange={(e) => setLink(e.target.value)}/>
                    <TextField label="Price" value={price} onChange={(e) => setPrice(e.target.value)}/>
                    <TextField type="password" label="Auth" value={auth} onChange={(e) => setAuth(e.target.value)}/>
                    <Button variant="contained" onClick={addData}>Add Item</Button>
                </Paper>
                
            </form>

        </div>
    );
}

export default Items;

